/* khula-800 - latin_cyrillic */
@font-face {
  font-family: 'Khula';
  font-style: normal;
  font-weight: 800;
  src: local('Khula Bold'), local('Khula-Bold'),
    url('../assets/fonts/khula-v5-latin-800.woff2') format('woff2'),
    /* Chrome 26+, Opera 23+, Firefox 39+ */
      url('../assets/fonts/khula-v5-latin-800.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
/* libre-baskerville-italic-regular - latin_cyrillic */
@font-face {
  font-family: 'Libre Baskerville';
  font-style: italic;
  font-weight: 400;
  src: local('Libre Baskerville Italic'), local('Libre Baskerville-Italic'),
    url('../assets/fonts/libre-baskerville-v7-latin-italic.woff2')
      format('woff2'),
    /* Chrome 26+, Opera 23+, Firefox 39+ */
      url('../assets/fonts/libre-baskerville-v7-latin-italic.woff')
      format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
